body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    height: 100%;
    width: 100%;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html, body, #root {
    height: 100%;
}

body {
    background-color: #333;
    color: #eee;
    font-family: "Courier New", Helvetica, Arial;
    font-size: 3vmin;
}

/** Buttons **/
.btn {
    -webkit-align-self: center;
            align-self: center;
    justify-self: center;
    background-color: transparent;
    border-radius: 2em;
    border: 0.2em solid #1ecd97;
    color: #1ecd97;
    cursor: pointer;
    font-size: 3vmin;
    padding: 0.7em 1.5em;
    text-transform: uppercase;
    transition: all 0.25s ease;
    margin: 1% auto;
}

.btn:hover {
    background: #1ecd97;
    color: #333;
}

/** Background **/

.background {
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-size: cover;
    background-position: center center;
    -webkit-filter: blur(8em) opacity(0.6);
            filter: blur(8em) opacity(0.6);
    position: absolute;
}

.main-wrapper {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    height: 100%;
    margin: 0 auto;
    -webkit-justify-content: center;
            justify-content: center;
    position: relative;
    width: 80%;
    z-index: 1;
}

.container-column {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 100%;
    width: 100%;
}

.main-container {
    -webkit-flex: 1 1;
            flex: 1 1;
}

.welcome {
    padding: 3% 5% 0 5%;
    -webkit-align-content: center;
            align-content: center;
}

header {
    margin: 25px 0 25px 0;
    display: -webkit-flex;
    display: flex;
    text-align: left;
    width: 100%;
    -webkit-order: 0;
            order: 0;
}

.header-btn {
    padding: 5px;
    background-color: transparent;
    border: 0.2em solid #1ecd97;

    color: #1ecd97;
    cursor: pointer;
    text-transform: uppercase;
    transition: all 0.25s ease;
    margin: 0.5em;
}

.header-btn:hover {
    background: #1ecd97;
    color: #333;
}

.remove-btn{
    color: #c60707;
    border-color: #c60707;
}

.remove-btn:hover {
    background: #c60707;
    color: #000;
}

.download-btn {
    color: #3068c1;
    border-color: #3068c1;
}

.download-btn:hover {
    background: #3068c1;
    color: #FFFFFF;
}


.download-btn {
    float: right;
}

.remove-btn {
    margin-left: auto;
}

.max-width {
    width: 100%;
}
#welcome {
    font-family: "Courier New", Arial, serif;
    text-align: justify;
    width: 90%;
}

@media screen and (min-width: 720px){
    #welcome {
        width: 80%;
    }
}

@media screen and (min-width: 1200px){
    #welcome {
        width: 60%;
    }
}

main {
    font-size: 0.8em;
}

#sign {
    text-align: right;
}
.album {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 1%;
}

.album-name, .album-artist {
    width: 100%;
    text-align: center;
    max-width: 300px;
    -webkit-align-self: center;
            align-self: center;
}

img {
    outline: 0;
    max-width: 100%;
    height: auto;
}

.folder-selection {
    max-width: 250px;
}
.album-container {
    -webkit-order: 2;
            order: 2;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}

@media screen and (min-width: 600px){
    .album-container {
        width: 50%;
        -webkit-order: 1;
                order: 1;
        -webkit-flex-grow: 1;
                flex-grow: 1;
    }
}

@media screen and (min-width: 900px){
    .album-container {
        -webkit-flex-grow: 2;
                flex-grow: 2;
    }
}

@media screen and (min-width: 1200px){
    .album-container {
        -webkit-flex-grow: 3;
                flex-grow: 3;
    }
}

@media screen and (min-width: 1500px){
    .album-container {
        -webkit-flex-grow: 4;
                flex-grow: 4;
    }
}
.folder-container {
    -webkit-order: 1;
            order: 1;
    width: 100%;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-self: flex-start;
            align-self: flex-start;
}

#folder-creator {
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

@media screen and (min-width: 600px){
    .folder-container {
        width: 50%;
        -webkit-order: 2;
                order: 2;
    }
}


@media screen and (min-width: 900px){
    .folder-container {
        width: 33.33%;
    }
}

@media screen and (min-width: 1200px){
    .folder-container {
        width: 25%;
    }
}

@media screen and (min-width: 1500px){
    .folder-container {
        width: 20%;
    }
}
.folder {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-content: center;
            align-content: center;
    -webkit-align-self: center;
            align-self: center;
}

.folder-name {
    max-width: 250px;
    word-break: break-word;
    -webkit-align-self: center;
            align-self: center;
}

.foldered-album-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
}

.hidden{
    visibility: hidden;
    height: 0;
}

img {
    outline: 0;
    max-width: 100%;
    height: auto;
}

#footer {
    -webkit-order: 100;
            order: 100;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    position: -webkit-sticky;
    position: sticky;
    right: 0;
    bottom: 0;
}

.link {
    font-size: 64px;
    margin: 0 5px;
}

.link:hover {
    cursor: pointer;
}

#footer a:visited {
    color: #FFF;
}
